o;\n}\n.page-wrapper{\n    position: inherit;\n    margin:auto;\n    justify-content: center;\n    max-width: 80%;\n}\n/* /ボックス要素 */\n.box-wrapper{\n    width: 80%;\n    display: flex;\n    margin: 0;\n    width: 100%;\n    justify-content: center;\n    flex-wrap: wrap; \n  }\n  /* ▼モーダル▼ */\n  .modal-open {\n    background: rgba(0, 0, 0, 0.4);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    position: fixed;\n    top: 0;\n    left: 0;\n    z-index: 100;\n    width: 100vw;\n    height: 100vh;\n  }\n  .modal-close {\n    position: fixed;\n    top: 0;\n    right: 20px;\n    color: #fff;\n    z-index: 101;\n    cursor: pointer;\n    font-size: 24px;\n  }\n  /* ▲モーダル▲ */\n  \n/* フォント */","<template>\n  <section>\n    <div class=\"page-wrapper\">\n      <h1 class=\"title\">Profile</h1>\n      <div class=\"line-eq-wrapper\">\n        <div class=\"profile-img\">\n          <img class=\"img-size\" src=\"../assets/myface.png\" tittle=\"プロフィール画像\">\n        </div>\n        <table>\n          <tbody>\n            <tr v-for=\"(item, i) in items\" :key=\"i\">\n              <th>{{ item.title }}</th>\n              <td v-if=\"item.url\" class=\"\">\n                <a :href=\"item.url\" target=\"_blank\" rel=\"noopener noreferrer\" class=\"url-color\">\n                  {{ item.value }}\n                </a>\n              </td>\n              <td v-else class=\"last-td\">\n                {{ item.value }}\n              </td>\n            </tr>\n          </tbody>\n        </table>\n      </div>\n    </div>\n  </section>\n</template>\n\n<script>\nexport default {\n  name: 'Profile',\n  data () {\n    return {\n      items: [\n        {title: 'Name', value: '広田楓(ひろたかえで)', url: ''},\n        {title: 'University', value: '公立はこだて未来大学 情報システム科学部 複雑系コース', url: ''},\n        {title: 'E-mail', value: 'b1017222@fun.ac.jp', url: ''},\n        {title: 'GitHub', value: 'こちらからどうぞ！', url: 'https://github.com/b1017222'},\n        {title: 'Comment', value: '初めまして。北海道函館出身の大学生です。1年半程前にプログラミングに興味を持ち始め、そこからインターンでサーバーサイド言語に触れたのを機にバックエンドからの学習を進めていました。一年前からプログラミングのアルバイトを始め、主にスマホ向けアプリやwebアプリケーションの受託開発に携わっています。そのため普段はバックエンドからフロントエンドまで幅広く案件に触れる機会が多いです。また最近ではwebアプリケーションに興味を持ち始め、モダンjsやデータベースについて学習しています。', url: ''}\n      ]\n    }\n  }\n}\n</script>\n\n<style scoped>\n@import '../assets/styles/base.css';\n\n.line-eq-wrapper{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    box-sizing:content-box;\n}\n.img-size{\n    width: 400px;\n}\n.profile-img{\n     display: inline-block;\n}\np{\n  margin: 0 auto;\n }\n/* ▼テーブル▼ */\ntable {\n    display: inline-block;\n    margin-left: 32px;\n    text-align: left;\n}\ntr {\n    height: 52px;\n}\nth {\n    padding-right: 20px;\n    font-weight: normal;\n    color: rgb(0, 0, 0);\n}\ntd {\n    font-size: 20px;\n    padding: 5px 8px 5px 0;\n}\na {\n  text-decoration: none;\n}\n.url-color{\n    color: rgb(216, 80, 18);\n}\n/* ▲テーブル▲ */\n\n/* スマホ */\n/* @media (max-width: 670px) {\n    td {\n      display: block;\n      font-size: 16px;\n      width: 100%;\n      padding: 5px 0;\n      border-bottom: none;\n    }\n    .line-eq-wrapper{\n    }\n} */\n</style>\n"],"sourceRoot":""}]);

